import './Ocean_Warming.css';
import Toolbar from '../../components/Toolbar/Toolbar';
import React, { Component } from 'react';
import SideDrawer from '../../components/SideDrawer/SideDrawer';
import Backdrop from '../../components/Backdrop/Backdrop';
import SideMenuOcean from '../../components/SideMenuOcean/SideMenuOcean';
import OceanBg from '../../components/OceanBg/OceanBg';
import styled from 'styled-components';
import InfoOceanwarming from '../../components/InfoOceanwarming/InfoOceanwarming';
import InfoHypoxia from '../../components/InfoHypoxia/InfoHypoxia';
import InfoLobster from '../../components/InfoLobster/InfoLobster';
import InfoArcticicemin from '../../components/InfoArcticicemin/InfoArcticicemin';
import InfoHurricanes from '../../components/InfoHurricanes/InfoHurricanes';
import {Link} from 'react-router-dom';
import back from '../../assets/back.png';

const Tree = styled.p`
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  font-family:roboto;
  font-size: 1.5rem;
  font-weight:300;
  color: #ffffff;
  margin: 0em 0em 0.9em;
  letter-spacing: .6px;
  margin-left:0px;
  
`;


const Stacker= styled.div`
  display: flex;
  flex-direction:row;
  height: auto;
  justify-content: space-around;
  @media (max-width: 1130px) {
    flex-direction: column;
  }
`;
const Left = styled.div`
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display:flex;
  justify-content:center;
`;

const Threat = styled.div`
    width:40vw;
  align-self:center;
  @media (max-width: 1130px) {
    width:85%;
  }
   @media (max-width: 768px) {
    width:100%;
  }
`;

const Right = styled.div`
    display:flex;
  justify-content:center;
`;

const Impacts = styled.div`
  width:40vw;
  flex-direction:column;
  justify-content:center;

    @media (max-width: 1130px) {
    width:85%;
  }
   @media (max-width: 768px) {
    width:100%;
  }
`;

const Contentwrapper = styled.div`
  margin-left:15px;
  margin-top:15px;
  margin-right:15px;
  margin-bottom:15px;

`;

const Impactwrapper = styled.div`
  margin-left:0px;
  margin-top:15px;
  margin-right:0px;
  margin-bottom:15px;
    transform: scale(1);
  transition: all 0.5s;
      &:hover {
  transform: scale(1.04);
    transition: all 0.5s;
  
}
`;


const Backico = styled.img`
  margin-left:0px;
  justify-content:center;
  align-self:center;
  object-fit:contain;
  width:20px;
  height:auto;
  margin-right:5px;
`;

class Ocean_Warming extends Component{


  state = {
    sideDrawerOpen:false
  };

drawerToggleClickHandler = ()=> {

  this.setState((prevState) =>{
    return{sideDrawerOpen: !prevState.sideDrawerOpen};
});
};

backdropClickHandler = () => {
    this.setState({sideDrawerOpen:false});
};

  render(){

   
    let backdrop;

    if (this.state.sideDrawerOpen){
   
      backdrop= <Backdrop click={this.backdropClickHandler}/>
    }
    return(
      
        <body> 
          <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
          <SideDrawer show={this.state.sideDrawerOpen} />
          {backdrop}
          <SideMenuOcean />
          <OceanBg/>
          
          <main className = "bod">
         
         <Stacker>
            
      <Contentwrapper>
          <Left>
           <Threat>  
           <Contentwrapper>
          <Tree><Link to="/Oceans"><Backico src= {back}/></Link><Link to="/Oceans" className="link">Oceans</Link>/ Warming</Tree>   
          </Contentwrapper>        
              <InfoOceanwarming/>
           </Threat>
          </Left>
      </Contentwrapper>
     
      <Contentwrapper>
          <Right>
           <Impacts>

              <Impactwrapper><Link to="/Oceans_Hurricanes"><InfoHurricanes/></Link></Impactwrapper>  
              <Impactwrapper><Link to="/Oceans_Hypoxia"><InfoHypoxia/></Link></Impactwrapper>
              <Impactwrapper><Link to="/Land_Arctic-ice-minimum"><InfoArcticicemin/></Link></Impactwrapper>
              <Impactwrapper><Link to="/Oceans_Lobster"><InfoLobster/></Link></Impactwrapper>  

           </Impacts>
          </Right> 
      </Contentwrapper>

      </Stacker>
          
          </main>
        </body>
      
      );

  } 
}

export default Ocean_Warming;
